
.dashboard{
  background-image: url('../src/utils/image/quiz-night.png');
  background-size: auto;
}

.login{
  background-size: auto;
}

.bg_image{
  background-image: url('../src/utils/image/background.jpg');
  background-size: auto;
  background-repeat: round;
  color: #dee1e9;
}.App {
  text-align: center;
}

.know-more {
  background-image: url('../src/utils/image/background1.png');
  background-color: #ffffffd0;
  padding: 3rem;
  
  color: rgb(61, 60, 30);

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #92d7ec;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
